<template>
  <page-content>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="24">
          <a-col :md="6" :sm="12">
            <a-form-item label="关键字">
              <a-input v-model="queryParams.keywords" placeholder="输入合作方名" style="width: 250px" />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-form-item label="封禁状态">
              <a-select
                v-model="queryParams.locked"
                placeholder="请选择"
                default-value="0"
                style="width: 200px"
              >
                <a-select-option value="">全部合作方</a-select-option>
                <a-select-option value="0">非封禁合作方</a-select-option>
                <a-select-option value="1">封禁合作方</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-button type="primary" @click="refresh(false)">查询</a-button>
            <a-button style="margin-left: 8px" @click="resetSearchForm()">重置</a-button>
          </a-col>
        </a-row>
      </a-form>
    </div>
    <div class="table-operator">
      <!-- <a-button type="primary" icon="plus" @click="handleAdd">新建</a-button> -->
      <permission-button
        code="third-party-create"
        title="新建"
        @handle-click="handleAdd"
        type="modal"
        class="pull-left"
      />
      <div class="batch-control pull-left">
        <!-- 可以使用自定义指令进行按钮鉴权限  -->
        <!-- 后端对操作权限可以固定设置：增、删、改、查  -->
        <!-- 后端对操作权限也可以详细设置：增、删、改、查、封禁、解封、批量封禁、批量解封  -->
        <a-dropdown v-if="selectedRowKeys.length > 0">
          <a-menu slot="overlay">
            <!-- lock | unlock -->
            <a-menu-item key="1" @click="lockThirdParties()"> <a-icon type="lock" />封禁 </a-menu-item>
            <a-menu-item key="2" @click="unlockThirdParties()"> <a-icon type="unlock" />解禁 </a-menu-item>
          </a-menu>
          <a-button style="margin-left: 8px">
            批量操作
            <a-icon type="down" />
          </a-button>
        </a-dropdown>
      </div>
    </div>
    <a-table
      :columns="columns"
      :data-source="thirdParties"
      :pagination="false"
      :rowSelection="rowSelection"
      ref="table"
      :loading="loading"
      rowKey="id"
    >
      <template slot="partner_name" slot-scope="text, record">
        <span :class="record.locked == 1 ? 'del-span' : ''">{{ text }}</span>
      </template>
      <template slot="key_sceret" slot-scope="text, record">
        <div>{{ record.partner_key }}</div>
        <div v-if="record.partner_sceret">
          <a-input-password :value="record.partner_sceret" read-only class="pwd-border-none" />
        </div>
      </template>
      <template slot="live_providers" slot-scope="text, record">
        <a-tooltip :title="record.live_providers.join('、')" :get-popup-container="getPopupContainer">
          <a-tag v-for="i of record.live_provider_titles" color="blue" :key="i">{{ i }}</a-tag>
        </a-tooltip>
      </template>
      <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
      <span slot="date" slot-scope="date">{{ date | filterDate(date) }}</span>
      <template slot="operation" slot-scope="text, record">
        <space>
          <!-- :routeLink="`/third-party/${record.id}/edit`" -->
          <permission-button
            code="third-party-edit"
            @handle-confirm="handleEdit(record.id)"
            title="重置key/sceret"
            v-if="record.locked == 0 && hasEdit"
            type="confirm"
          />
          <permission-button
            :confirm="true"
            :isCheck="false"
            @handle-confirm="handleLock(record.id)"
            title="封禁"
            v-if="record.locked == 0 && hasDelete"
            type="confirm"
          />
          <permission-button
            :confirm="true"
            :isCheck="false"
            @handle-confirm="handleUnLock(record.id)"
            title="解禁"
            v-if="record.locked == 1"
            type="confirm"
          />
        </space>
      </template>
    </a-table>
    <lz-pagination :page="paginator" @change="changePaginator" @showSizeChange="showSizeChange" :autoPush="false" />
    <third-party-form
      ref="thirdPartyFormModal"
      :visible="visible"
      :loading="confirmLoading"
      @cancel="handleCancel"
      @ok="handleOk"
    />
  </page-content>
</template>

<script>
import { message } from 'ant-design-vue'
import {
    getThirdParties,
    createThirdParty,
    editThirdParty,
    lockThirdParty,
    unlockThirdParty,
    lockThirdParties,
    unlockThirdParties
} from '@/api/third-party'
import Space from '@c/Space'
import LzPagination from '@c/LzPagination'
import PageContent from '@c/PageContent'
import LzPopconfirm from '@c/LzPopconfirm'
import { getRouteByCode } from '@/libs/utils'
import moment from 'moment'
import PermissionButton from '@/components/PermissionButton.vue'
const thirdPartyForm = () => import('./Form.vue')
// import thirdPartyForm from './CreateForm.vue'
export default {
    name: 'Index',
    scroll: true,
    components: {
        LzPopconfirm,
        PageContent,
        LzPagination,
        Space,
        PermissionButton,
        thirdPartyForm
    },
    data () {
        return {
            visible: false,
            confirmLoading: false,
            loading: false,
            queryParams: {
                keywords: '',
                locked: ''
            },
            selectedRowKeys: [],
            selectedRows: [],
            columns: [
                {
                    title: 'ID',
                    dataIndex: 'id',
                    key: 'id',
                    with: 40
                    // scopedSlots: { customRender: 'name' },
                },
                {
                    title: '合作方名',
                    dataIndex: 'partner_name',
                    key: 'nickname',
                    width: 100,
                    scopedSlots: { customRender: 'partner_name' }
                },
                {
                    title: 'KEY/SCERET',
                    dataIndex: 'partner_key',
                    key: 'email',
                    ellipsis: true,
                    width: 250,
                    scopedSlots: { customRender: 'key_sceret' }
                },
                {
                    title: '接入平台',
                    dataIndex: 'live_providers',
                    key: 'live_providers',
                    ellipsis: true,
                    scopedSlots: { customRender: 'live_providers' }
                },
                {
                    title: '服务ip',
                    dataIndex: 'server_ip',
                    key: 'server_ip'
                },
                {
                    title: '使用有效期',
                    dataIndex: 'expired_time',
                    key: 'expired_time',
                    ellipsis: true,
                    scopedSlots: { customRender: 'date' }
                },
                {
                    title: '添加时间',
                    dataIndex: 'createdTime',
                    key: 'createdTime',
                    ellipsis: true,
                    scopedSlots: { customRender: 'time' }
                },
                {
                    title: '操作',
                    dataIndex: 'operation',
                    scopedSlots: { customRender: 'operation' }
                }
            ],
            thirdParties: [],
            paginator: null,
            hasEdit: false,
            hasDelete: false
        }
    },
    mounted () {
        this.loadData()
        this.checkPermissions()
    },
    computed: {
        rowSelection () {
            return {
                selectedRowKeys: this.selectedRowKeys,
                onChange: this.onSelectChange
            }
        }
    },
    methods: {
        lockThirdParties () {
            const rows = this.selectedRows
            const ids = []
            for (const key in rows) {
                const row = rows[key]
                if (parseInt(row.locked) === 0) {
                    ids.push(row.id)
                }
            }
            if (!ids.length) {
                return false
            }

            const that = this
            this.$confirm({
                title: '确认封禁吗?',
                async onOk () {
                    const res = await lockThirdParties(ids)
                    console.log('res:', res)
                    message.success('操作成功')
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        unlockThirdParties () {
            const rows = this.selectedRows
            const ids = []
            for (const key in rows) {
                const row = rows[key]
                if (parseInt(row.locked) === 1) {
                    ids.push(row.id)
                }
            }
            if (!ids.length) {
                return false
            }

            const that = this
            this.$confirm({
                title: '确认解除封禁吗?',
                async onOk () {
                    const res = await unlockThirdParties(ids)
                    console.log('res:', res)
                    message.success('操作成功')
                    that.refresh()
                },
                onCancel () {},
                class: 'test'
            })
        },
        getPopupContainer (trigger) {
            return trigger.parentElement
        },
        checkPermissions () {
            const vueRouters = this.$store.state.vueRouters.vueRouters
            let route = getRouteByCode(vueRouters, 'third-party-edit')
            if (route) {
                this.hasEdit = true
            }
            route = getRouteByCode(vueRouters, 'third-party-lock')
            if (route) {
                this.hasDelete = true
            }
        },
        handleEdit (id) {
            const that = this
            this.$confirm({
                title: '确认重置?',
                // content: h => <div style="color:red;">${id}</div>,
                async onOk () {
                    await editThirdParty(id, {})
                    message.success('操作成功')
                    that.refresh()
                },
                onCancel () {
                    // console.log('Cancel');
                },
                class: 'test'
            })
        },
        handleAdd () {
            this.$refs.thirdPartyFormModal.title = '创建合作方'
            this.$refs.thirdPartyFormModal.resetForm()
            this.visible = true
            this.confirmLoading = false
        },
        handleLock (id) {
            const that = this
            this.$confirm({
                title: '确认封禁该合作方?',
                // content: h => <div style="color:red;">${id}</div>,
                async onOk () {
                    const res = await lockThirdParty(id)
                    console.log('res:', res)
                    message.success('操作成功')
                    that.refresh()
                },
                onCancel () {
                    // console.log('Cancel');
                },
                class: 'test'
            })
        },
        handleUnLock (id) {
            const that = this
            this.$confirm({
                title: '确认解除封禁该合作方?',
                async onOk () {
                    const res = await unlockThirdParty(id)
                    console.log('res:', res)
                    message.success('操作成功')
                    that.refresh()
                },
                onCancel () {
                    // console.log('Cancel');
                },
                class: 'test'
            })
        },
        handleCancel () {
            this.visible = false
        },
        handleOk () {
            const model = this.$refs.thirdPartyFormModal.model
            this.confirmLoading = true
            this.$refs.thirdPartyFormModal.$refs.thirdPartyForm.validate((valid) => {
                if (!valid) {
                    return false
                }
                if (!model.id) {
                    createThirdParty(model).then((response) => {
                        message.success('保存成功')
                        this.visible = false
                        this.confirmLoading = false
                        this.refresh()
                    })
                }
            })
        },
        resetSearchForm () {
            this.queryParams = {
                keywords: '',
                locked: ''
            }
        },
        refresh (resetSearch = true) {
            this.loadData()
            if (resetSearch) {
                this.resetSearchForm()
            }
        },
        showSizeChange (current, perPage) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: current,
                limit: perPage
            })
            this.refresh()
        },
        changePaginator (page, pageSize) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: page,
                limit: pageSize
            })
            this.refresh()
        },
        async loadData () {
            this.loading = true
            const { thirdParties, paginator } = await getThirdParties(this.queryParams)
            this.thirdParties = thirdParties
            this.paginator = paginator
            this.loading = false
        },
        dateFormat (row, column) {
            var date = row[column.property]
            console.log('date:', date)
            if (!date) {
                return ''
            }
            return moment(date).format('YYYY-MM-DD HH:mm:ss')
        },
        onSelectChange (selectedRowKeys, selectedRows) {
            this.selectedRowKeys = selectedRowKeys
            this.selectedRows = selectedRows
        }
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        },
        filterDate (date) {
            if (parseInt(date) === 0) {
                return '长期有效'
            }

            return moment(date).format('YYYY-MM-DD')
        }
    }
}
</script>
<style lang="less">
.del-span {
    text-decoration: line-through;
    color: red;
}
.pwd-border-none {
    .ant-input {
        border: none;
        padding-left: 0px;
        &:focus {
            border: none;
        }
    }
}
</style>
